#search-input {
  width: 100%;
  height: 8px;
  color: rgb(10, 10, 35);
  padding: 10px;
  margin: auto;
}

.algolia-autocomplete {
  width: 100%;
}

.algolia-autocomplete .aa-input, .algolia-autocomplete .aa-hint {
  width: 100%;
}

.algolia-autocomplete .aa-hint {
  color: #999;
}

.algolia-autocomplete .aa-dropdown-menu {
  width: 100%;
  background-color: #fff;
  border: 1px solid #999;
  border-top: none;
}

.algolia-autocomplete .aa-dropdown-menu .aa-suggestion {
  cursor: pointer;
  padding: 5px 4px;
}

.algolia-autocomplete .aa-dropdown-menu .aa-suggestion.aa-cursor {
  background-color: #B2D7FF;
}

.algolia-autocomplete .aa-dropdown-menu .aa-suggestion em {
  font-weight: bold;
  font-style: normal;
}

/* remove default styling for hits */
.aa-dataset-1 a { text-decoration: none; }
.aa-dataset-1 a:visited { text-decoration: none; }
.aa-dataset-1 a:hover { text-decoration: none; }
.aa-dataset-1 a:focus { text-decoration: none; }
.aa-dataset-1 a:hover, a:active { text-decoration: none; }

/* style hits */
.algolia-result {
  padding: 10px;
}

.aa-dataset-1 a {
  color: rgb(10, 10, 35);
}

.aa-dropdown-menu {
  max-height: 70vh;
  overflow: auto;
}
